.calculator {
  width: 100%; /* Adaptive width */
  max-width: 420px; /* Maintain Casio-like size */
  margin: 0 auto;
  padding: 0.4rem;
  border: 2px solid #333;
  border-radius: 8px;
  background: linear-gradient(to bottom, #f0f0f0, #d0d0d0);
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  font-family: 'Courier New', monospace;
}

.display {
  background: #000;
  color: #0f0;
  font-size: 1.2rem;
  padding: 0.4rem;
  text-align: left;
  min-height: 1.5rem;
  border: 1px solid #333;
  border-radius: 4px;
  margin-bottom: 0.4rem;
  overflow: hidden;
  white-space: pre;
}

.key-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(8, auto);
  gap: 0.1rem;
}

.key {
  padding: 0.6rem 0.3rem;
  font-size: 0.8rem;
  border: 1px solid #666;
  border-radius: 2px;
  cursor: pointer;
  transition: background 0.1s;
  background: #c0c0c0;
  text-align: center;
}

.key:hover { background: #d0d0d0; }
.key:active { background: #a0a0a0; }

.num-key { background: #e0e0e0; }
.op-key { background: #b0b0b0; }
.sci-key { background: #90c0f0; }
.f-key { background: #f0f090; }

.graph-section {
  margin-top: 0.4rem;
  height: 200px; /* Increased from 180px to fit graph better */
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.history {
  margin-top: 0.4rem;
  font-size: 0.7rem;
  max-height: 80px;
  overflow-y: auto;
}

.history h4 { margin: 0 0 0.2rem 0; font-size: 0.8rem; }
.history p { margin: 0.1rem 0; font-family: monospace; }

@media (max-width: 450px) {
  .calculator { padding: 0.3rem; }
  .key { padding: 0.5rem 0.2rem; font-size: 0.7rem; }
  .graph-section { height: 150px; } /* Adjusted for mobile */
}
